.btn {
    margin: 10px 0;
    padding: 4px 15px;
    background: #556080;
    color: #fff;
    outline: none;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition:  background 0.2s;
}

.btn:hover {
    background: #4259c1;
    transition:  background 0.2s;
}